<template>
    <v-row class="qiong-pt-10">
        <v-col cols="12" class="pa-0 qiong-td">
            <div class="qiong-wide-9">
                <qiong-icon-outline @done_Father="done"></qiong-icon-outline>
            </div>
            <div class="qiong-wide-90">
                <div class="qiong-txt-page-header">
                    <slot name="header"></slot>
                </div>
            </div>
        </v-col>
        <v-col class="pa-0 qiong-td">
            <div class="qiong-wide-9"></div>
            <div class="qiong-wide-91 qiong-pt-5">
                <slot name="content"></slot>
            </div>
        </v-col>
        <v-col cols="12" class="qiong-td qiong-pt-10 mt-1">
            <div class="qiong-wide-2"></div>
            <div class="qiong-wide-96">
                <qiong-page-ling></qiong-page-ling>
            </div>
            <div class="qiong-wide-2"></div>
        </v-col>
    </v-row>
</template>

<script>
import QiongPageHeader from '../../../../../oilsdemo/src/components/Qiong/QiongPageHeader.vue'
import QiongIconFlat from '../Button/QiongIconFlat.vue'
import QiongIconOutline from '../Button/QiongIconOutline.vue'
import QiongPageLing from '../QiongPageLing.vue'
    export default {
  components: { QiongPageLing, QiongPageHeader, QiongIconFlat, QiongIconOutline },
        props: {
            
        },
        inject: [ 'reload' ],
        name: '',
        data() {
            return {
                
            }
        },
        methods: {
            done() {
                
                this.$router.back()
            }
        },
    }
</script>

<style lang="sass" scoped>

</style>


