<template>
    <div v-if="item" class="qld-content">
        <slot></slot>
    </div>
    <qiong-loading :bigger="loading" v-else></qiong-loading>
</template>

<script>
import Loading from '../Data/Loading.vue'
import QiongLoading from './Ui/QiongLoading.vue'
    export default {
  components: { Loading, QiongLoading },
        props: {
            item: {
                type: Object
            },
            loading: {
                type: Number,
                default: 0
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.ld-content
    display: inline-block
</style>


