<template>

    <div>
        <v-btn
            class="mx-2"
            fab
            outlined

            x-small
            :color="color"
            :class="extra_calss"

            @click="done"
        >
            <v-icon>
                {{ icon }}
            </v-icon>
        </v-btn>
    </div>

</template>

<script>
    export default {
        props: {
            icon: {
                type: String,
                default: 'mdi-chevron-left'
            },
            color: {
                type: String,
                default: ''
            },
            extra_calss: {
                type: String,
                default: ''
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        methods: {
            done(item) {
                this.$emit('done_Father', item)
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


